import React from "react";
import axios from 'axios';
import Movie from "../components/Movie";
import "./Home.css";

class Home extends React.Component {
    state = {
        isLoading: true,
        movie: []
    };
    // axios.get 데이터 로딩이 느리니까 비동기로 써서 이 작업이 끝날때 
    // 까지 기다렸다가 작동해라는 뜻.
    getMovies = async () => {
        const {
            data: {
                data: { movies }
            }
        } = await axios.get("https://yts-proxy.nomadcoders1.now.sh/list_movies.json?sort_by=rating");
        this.setState({ movies, isLoading: false }) // setState를 만들기 위해선 new state 가 필요한데 여기선 위에서 써준 state 안의 movies 와 axios 안의 movies 를 가지고 만드는데 이름같으니까 하나로 생략됨, 위에서 선언한 state안에 두개의 설정을 setState로 선언해서 상태를 바꿔줌 
    }
    componentDidMount() {
        this.getMovies();
    }
    render() {
        const { isLoading, movies } = this.state;
        return <section className="container">
            {isLoading ? (
                <div className="loader">
                    <span className="loader__text">Loading...</span>
                </div>) : (<div className="movies">
                    {movies.map(movie => (
                        <Movie
                            key={movie.id}
                            id={movie.id}
                            year={movie.year}
                            title={movie.title}
                            summary={movie.summary}
                            poster={movie.medium_cover_image}
                            genres={movie.genres}
                        />
                    ))}
                </div>
                )}
        </section>;

        // 위와 같이 object를 풀어줄 때 map함수를 사용하고,
        // 또, jsx에서는 props를 통해서 값을 전달합니다. key는 표현되지 않지만 필수props입니다.
    }

}
export default Home;
